jQuery(document).ready(function ($) {

		/* scroll header */
	$(window).scroll(function () {
		if ($(this).scrollTop() > 100) {
			$('#header').addClass('header__scrolled');
		} else {
			$('#header').removeClass('header__scrolled');
		}
	});

	if ($(window).scrollTop() > 100) {
		$('#header').addClass('header__scrolled');
	}

	/* Slide-Pro */

	$('#my-slider').sliderPro({
		width: '100%',
		height: '100vh',
		fade: true,
		fadeDuration: 3000,     /*    */
		autoplayDelay: 8000,   /*    */
		autoplayOnHover: 'none',  /*        */
		arrows: true,            /*    */
		fadeArrows: false,         /*    */
		buttons: false,          /*   */
		autoplay: true,
		
		
	});

});